<template>
  <div class="add-blog">
  	<form class=" card offset-3 col-6" v-show="!isTrue">
  		<h2 class="mt-4 text-center">写博客</h2>
    	<div class="form-group">
    		<label>主题：</label>
    		<input type="text" class="form-control" v-model="blog.name">
    	</div>
    	<div class="form-group">
    		<label>内容：</label>
    		<textarea rows="5" class="form-control" v-model="blog.content"></textarea>
    	</div>
		<label>分类：</label>
		<div class="form-check">
			<label>Vue.js</label>
			<input type="checkbox" class="mr-2" value="Vue.js" v-model="blog.types">
			<label>Angular4</label>
			<input type="checkbox" class="mr-2" value="Angular4" v-model="blog.types">
			<label>Node.js</label>
			<input type="checkbox" class="mr-2" value="Node.js" v-model="blog.types">
			<label>React.js</label>
			<input type="checkbox" class="mr-2" value="React.js" v-model="blog.types">
		</div>
		<div class="form-group mb-3">
			<label for="inputGroupSelect">作者：</label>
			<select class="custom-select" id="inputGroupSelect" v-model="blog.author">
				<option v-for="author in authors">{{ author }}</option>
			</select>
		</div>
		<button v-show="{isTrue : !isTrue}" @click.prevent="addBlog" type="button" class="btn btn-danger btn-lg">添加博客</button>
    </form>
	<div class="card offset-3 col-6 mt-4" v-show="isTrue">
		<div>
			<h3 v-if="isTrue" class="lead d-inline">您的博客发布成功！</h3>
			<router-link to="/" class="btn btn-outline-secondary float-right btn-sm">返回</router-link>
		</div>
		<div class="card-body">
			<h2 class="text-center mb-5">博客总览</h2>
			<p class="card-title">博客标题：{{ blog.name }}</p>
			<p class="card-text">博客内容：{{ blog.content }}</p>
			<p>博客分类：</p>
			<ul>
				<li v-for="type in blog.types">{{ type }}</li>
			</ul>
			<p class="text-muted">博客作者：{{ blog.author }}</p>
		</div>
	</div>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'AddBlog',
  components:{

  },
  data(){
  	return {
  		isTrue:false,
  		blog:{
  			name:'',
  			content:'',
  			types:[],
  			author:'',
  		},
  		authors:['Jack','Rose','Lucy']
  	}
  },
  methods:{
  	addBlog(){
  		let data = {
  			name:this.blog.name,
  			content:this.blog.content, 
  			types:this.blog.types,
  			author:this.blog.author,
  		};
  		axios.post('blog.json',data).then(res=>{
  			console.log(res)
  			this.isTrue = true;
  		})

  		
  	}
  }
};
</script>

<style scoped>

</style>
