<template>
  <div id="show">
	<div class="card offset-3 col-6">
		<div class="card-body">
			<h2 v-rainbow class="card-title text-center">博客总览</h2>
			<input type="text" class="form-control" placeholder="search blogs..." v-model="search">
		</div>
		<div class="jumbotron" v-for="blog in filterBlogs">
			<router-link :to="'/blog/'+blog.id">
        <h4 v-rainbow class="text-center">{{ blog.name | to-uppercase }}</h4>
      </router-link>
			<p>{{ blog.content | snippet }}</p>
		</div>
	</div>
  </div>
</template>

<script>

import axios from 'axios'

export default {
  name: 'home',
  components: {

  },
  data(){
  	return{
  		blogArr:[],
  		search:""
  	}
  },
  created(){
  	axios.get('blog.json').then(res=>{
  		let temp = [];
  		for(let key in res.data){
  			res.data[key].id = key;
  			temp.push(res.data[key]);
  		}
  		this.blogArr = temp;
  	})
  },
  computed:{
  	//搜索功能 
  	filterBlogs(){
  		return this.blogArr.filter((blog)=>{
  			return blog.name.match(this.search)
  		})
  	}
  }
};
</script>

