<template>
	<div class="card offset-3 col-6">
		<div class="card-body">
			<h2 v-rainbow class="card-title text-center">博客详情</h2>
		</div>
		<div class="jumbotron">
			<h4 v-rainbow class="text-center">博客标题：{{ blog.name }}</h4>
			<p>博客内容：{{ blog.content }}</p>
			<hr>
			<p>博客作者：<h3>{{ blog.author }}</h3></p>
			类型：
			<ul>
				<li v-for="type in blog.types">
					{{ type }}
				</li>
			</ul>
			<router-link :to="'/edit/'+id" tag="button" class="btn btn-outline-primary float-left">修改</router-link>
			<button @click="deleteBlog()" class="btn btn-outline-danger float-right">删除</button>
		</div>
	</div>
</template>

<script>
import axios from 'axios'
	export default{
		data(){
			return {
				blog:{},
				id:this.$route.params.id
			}
		},
		created(){
			axios.get('blog/'+this.id+'.json').then(res=>{
				this.blog = res.data;
			})
		},
		methods:{
			deleteBlog(){
				axios.delete('blog/'+this.id+'.json').then(res=>{
					this.$router.push('/');
				})
			}
		}
	};
</script>

